  <!-- ÂïÜÂÆ∂ÈîÄÈáèÁªüËÆ°ÁöÑÊ®™ÂêëÊü±Áä∂Âõæ -->
<template>
  <div class="com-container">
    <div class="com-chart" ref="seller_ref"></div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      chartInstance: null,
      chartData: null, // ÊúçÂä°Âô®ËøîÂõûÁöÑÊï∞ÊçÆ
      currentPage: 1, // ÂΩìÂâçÊòæÁ§∫ÁöÑÈ°µÊï∞
      totalPage: 0, // ‰∏ÄÂÖ±Â§öÂ∞ëÈ°µ
      timerId: null, // ÂÆöÊó∂Âô®Ê†áËØÜ
    };
  },
  created() {
    this.$socket.registerCallBack("sellerData", this.getData);
  },
  mounted() {
    this.initChart();
    // this.getData();
    this.$socket.send({
      action: "getData",
      socketType: "sellerData",
      chartName: "seller",
      value: "",
    });
    window.addEventListener("resize", this.screenAdapter);
    // Âú®È°µÈù¢Âä†ËΩΩÂÆåÊàêÊó∂ Ë∞ÉÁî®È°µÈù¢ÁõëÂê¨ÂáΩÊï∞
    this.screenAdapter();
  },
  destroyed() {
    clearInterval(this.timerId);
    // ÁªÑ‰ª∂ÈîÄÊØÅÊó∂ÂÄôÂèñÊ∂àÈ°µÈù¢ÁõëÂê¨Âô®
    window.removeEventListener("resize", this.screenAdapter);
    this.$socket.unRegisterCallBack("sellerData", this.getData);
  },
  methods: {
    initChart() {
      this.chartInstance = this.$echarts.init(
        this.$refs.seller_ref,
        this.theme
      );
      // ÂØπÂõæË°®ËøõË°åÂàùÂßãÂåñÈÖçÁΩÆ
      const initOption = {
        title: {
          text: "üëÄÂïÜÂÆ∂ÈîÄÂîÆÁªüËÆ°",
          left: 20,
          top: 20,
        },
        // ÈÖçÁΩÆÂùêÊ†áËΩ¥‰ΩçÁΩÆ
        grid: {
          top: "20%",
          left: "3%",
          right: "6%",
          bottom: "3%",
          containLabel: true, // ÂºÄÂêØË∑ùÁ¶ªÂåÖÂê´ÂùêÊ†áËΩ¥‰∏äÈù¢ÁöÑÊñáÂ≠ó
        },
        xAxis: {
          type: "value",
        },
        yAxis: {
          type: "category",
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "line",
            z: 0,
            lineStyle: {
              color: "#2d3443",
            },
          },
        },
        series: [
          {
            type: "bar",
            label: {
              show: true,
              position: "right",
              textStyle: {
                color: "white",
              },
            },
            itemStyle: {
              color: {
                // type: "linear", // Á∫øÊÄßÊ∏êÂèò
                x: 0,
                y: 0,
                x2: 1,
                y2: 0,
                colorStops: [
                  {
                    offset: 0,
                    color: "#5052ee", // 0%Â§ÑÁöÑÈ¢úËâ≤
                  },
                  {
                    offset: 1,
                    color: "#ab6ee5", // 100%Â§ÑÁöÑÈ¢úËâ≤
                  },
                ],
              },
            },
          },
        ],
      };

      this.chartInstance.setOption(initOption);
      // ÂØπÂõæË°®ÂØπË±°ËøõË°åÈº†Ê†á‰∫ã‰ª∂ÁöÑÁõëÂê¨
      this.chartInstance.on("mouseover", () => {
        clearInterval(this.timerId);
      });
      this.chartInstance.on("mouseout", () => {
        this.startInterval();
      });
    },
    // Ëé∑ÂèñÊúçÂä°Âô®ÁöÑÊï∞ÊçÆ
    getData(res) {
      // const { data: res } = await this.$http.get("seller");
      this.chartData = res;
      // ÂØπÊï∞ÁªÑËøõË°åÊéíÂ∫è
      this.chartData.sort((a, b) => {
        return a.value - b.value; // ‰ªéÂ∞èÂà∞Â§ß
      });
      // ÊØè5‰∏™ÂÖÉÁ¥†ÊòæÁ§∫‰∏ÄÈ°µ
      this.totalPage =
        this.chartData.length % 5 === 0
          ? this.chartData.length / 5
          : this.chartData.length / 5 + 1;
      this.updateChart();
      this.startInterval();
    },
    // Êõ¥Êñ∞ÂõæË°®
    updateChart() {
      const start = (this.currentPage - 1) * 5;
      const end = this.currentPage * 5;
      const showData = this.chartData.slice(start, end);

      const sellerNames = showData.map((item) => {
        return item.name;
      });
      const sellerValues = showData.map((item) => {
        return item.value;
      });
      const dataOption = {
        yAxis: {
          data: sellerNames,
        },

        series: [
          {
            data: sellerValues,
          },
        ],
      };
      this.chartInstance.setOption(dataOption);
    },
    startInterval() {
      if (this.timerId) {
        clearInterval(this.timerId);
      }
      this.timerId = setInterval(() => {
        this.currentPage++;
        if (this.currentPage > this.totalPage) {
          this.currentPage = 1;
        }
        this.updateChart();
      }, 3000);
    },
    // ÂõæË°®Ëá™ÈÄÇÂ∫î ÂΩìÊµèËßàÂô®Â§ßÂ∞èÂèëÁîüÂèòÂåñÊó∂Ë∞ÉÁî®
    screenAdapter() {
      const titleFontSize = (this.$refs.seller_ref.offsetWidth / 100) * 3.6;
      // ÂíåÂàÜËæ®ÁéáÂ§ßÂ∞èÁõ∏ÂÖ≥ÁöÑÈÖçÁΩÆÈ°π
      const adapterOption = {
        title: {
          text: "üëÄÂïÜÂÆ∂ÈîÄÂîÆÁªüËÆ°",
          textStyle: {
            fontSize: titleFontSize,
          },
        },
        tooltip: {
          // ËÉåÊôØËâ≤
          axisPointer: {
            lineStyle: {
              width: titleFontSize,
            },
          },
        },
        series: [
          {
            // ÊØèÊù°Êü±ÁöÑÂÆΩÂ∫¶
            barWidth: titleFontSize,
            itemStyle: {
              // ÊØèÊù°Êü±ÁöÑËæπËßí Â∑¶‰∏ä Âè≥‰∏ä Âè≥‰∏ã Â∑¶‰∏ã
              barBorderRadius: [0, titleFontSize / 2, titleFontSize / 2, 0],
            },
          },
        ],
      };
      this.chartInstance.setOption(adapterOption);
      // ÊâãÂä®Ë∞ÉÁî®ÂõæË°®ÂØπË±°ÁöÑresize
      this.chartInstance.resize();
    },
  },
  computed: {
    ...mapState(["theme"]),
  },
  watch: {
    theme() {
      this.chartInstance.dispose(); // ÈîÄÊØÅÂΩìÂâçÂõæË°®
      this.initChart(); // ‰ª•ÊúÄÊñ∞ÁöÑ‰∏ªÈ¢òÂàùÂßãÂåñÂõæË°®
      this.screenAdapter() // ÂÆåÊàêÂ±èÂπïÁöÑÈÄÇÈÖç
      this.updateChart() // Êõ¥Êñ∞ÂõæË°®ÁöÑÂ±ïÁ§∫
    },
  },
};
</script>
