export default class SocketService {
    static instance = null
    static get Instance() {
        if (!this.instance) {
            this.instance = new SocketService()
        }
        return this.instance
    }

    // ÂíåÊúçÂä°Á´ØËøûÊé•ÁöÑsocketÂØπË±°
    ws = null

    // Â≠òÂÇ®ÂõûË∞ÉÂáΩÊï∞
    callBackMapping = {}

    // Ê†áËØÜÊòØÂê¶ËøûÊé•ÊàêÂäü
    connected = false

    // ËÆ∞ÂΩïÈáçËØïÁöÑÊ¨°Êï∞
    sendRetryCount = 0

    // ÈáçÊñ∞ËøûÊé•Â∞ùËØïÁöÑÊ¨°Êï∞
    connectRetryCount = 0

    // ÂÆö‰πâËøûÊé•ÊúçÂä°Âô®ÁöÑÊñπÊ≥ï
    connect() {
        // ËøûÊé•ÊúçÂä°Âô®
        if (!window.WebSocket) {
            return console.log('ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅWebSocket');
        }
        this.ws = new WebSocket('ws://localhost:9998')

        // ËøûÊé•ÊàêÂäüÁöÑ‰∫ã‰ª∂
        this.ws.onopen = () => {
            console.log('WebSocketËøûÊé•ÊàêÂäü');
            this.connected = true
        }
        // 1.ËøûÊé•Â§±Ë¥•ÁöÑ‰∫ã‰ª∂
        // 2.ÂΩìËøûÊé•ÊàêÂäüÂêéÊúçÂä°Âô®ÂÖ≥Èó≠Êó∂
        this.ws.onclose = () => {
            console.log('WebSocketËøûÊé•Â§±Ë¥•');
            this.connected = false
            this.connectRetryCount++
            setTimeout(() => {
                this.connect()
            }, this.connectRetryCount * 500)
        }
        // ÂæóÂà∞ÊúçÂä°Á´ØÂèëÈÄÅËøáÊù•ÁöÑÊï∞ÊçÆ
        this.ws.onmessage = msg => {
            console.log('Êî∂Âà∞‰∫Ü‰ªéÊúçÂä°Âô®‰º†ÈÄíËøáÊù•ÁöÑÊï∞ÊçÆ');
            // console.log(msg.data);
            const recvData = JSON.parse(msg.data)
            const socketType = recvData.socketType
            // Âà§Êñ≠ÂõûË∞ÉÂáΩÊï∞ÊòØÂê¶Â≠òÂú®
            if (this.callBackMapping[socketType]) {
                const action = recvData.action
                if (action === 'getData') {
                    const realData = JSON.parse(recvData.data)
                    this.callBackMapping[socketType].call(this, realData)
                } else if (action === 'fullScreen') {
                    this.callBackMapping[socketType].call(this, recvData)
                } else if (action === 'themeChange') {
                    this.callBackMapping[socketType].call(this, recvData)
                }
            }
        }
    }

    // ÂõûË∞ÉÂáΩÊï∞ÁöÑÊ≥®ÂÜå
    registerCallBack(socketType, callBack) {
        this.callBackMapping[socketType] = callBack
    }

    // ÂèñÊ∂àÂõûË∞ÉÂáΩÊï∞
    unRegisterCallBack(socketType) {
        this.callBackMapping[socketType] = null
    }

    // ÂèëÈÄÅÊï∞ÊçÆÁöÑÊñπÊ≥ï
    send(data) {
        if (this.connected) {
            this.sendRetryCount = 0
            this.ws.send(JSON.stringify(data))
        } else {
            this.sendRetryCount++
            setTimeout(() => {
                this.send(data)
            }, this.sendRetryCount * 500)
        }
    }
}
