<template>
  <div class="com-container" @dblclick="revertMap">
    <div class="com-chart" ref="map_ref"></div>
  </div>
</template>
<script>
import axios from "axios";
import { getProvinceMapInfo } from "@/utils/map_utils";
import { mapState } from "vuex";
export default {
  data() {
    return {
      chartInstance: null,
      allData: null,
      titleFontSize: null,
      mapData: {},
    };
  },
  created() {
    this.$socket.registerCallBack("mapData", this.getData);
  },
  mounted() {
    this.initChart();
    // this.getData();
    this.$socket.send({
      action: "getData",
      socketType: "mapData",
      chartName: "map",
      value: "",
    });
    window.addEventListener("resize", this.screenAdapter);
    this.screenAdapter();
  },
  destroyed() {
    window.removeEventListener("resize", this.screenAdapter);
    this.$socket.unRegisterCallBack("mapData", this.getData);
  },
  methods: {
    async initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.map_ref, this.theme);
      const { data: res } = await axios.get(
        "http://localhost:8080/static/map/china.json"
      );
      this.$echarts.registerMap("chinaMap", res);
      const initOption = {
        title: {
          text: "üëÄÂêÑÁ±ªÂïÜÂÆ∂ÂàÜÂ∏É",
          left: 20,
          top: 20,
        },
        geo: {
          type: "map",
          map: "chinaMap",
          top: "5%",
          bottom: "5%",
          // ÊãñÂä®Áº©Êîæ
          roam: true,
          // ÂêçÁß∞ÊòæÁ§∫
          label: {
            show: true,
            color: "white",
          },
          itemStyle: {
            areaColor: "#2172bf",
            borderColor: "#333",
          },
        },
        legend: {
          left: "5%",
          bottom: "5%",
          // Âõæ‰æãÂûÇÁõ¥ÊïàÊûú
          orient: "vertical",
        },
      };
      this.chartInstance.setOption(initOption);
      this.chartInstance.on("click", async (arg) => {
        const provinceInfo = getProvinceMapInfo(arg.name);
        if (!provinceInfo.key) return;
        // Âà§Êñ≠ÂΩìÂâçÁÇπÂáªÁúÅ‰ªΩÂú∞ÂõæÁü¢ÈáèÊï∞ÊçÆÊòØÂê¶Â∑≤Â≠òÂú®
        if (!this.mapData[provinceInfo.key]) {
          const { data: res } = await axios.get(
            "http://localhost:8080" + provinceInfo.path
          );
          this.mapData[provinceInfo.key] = res;
          this.$echarts.registerMap(provinceInfo.key, res);
        }

        const changeOption = {
          geo: {
            map: provinceInfo.key,
          },
        };
        this.chartInstance.setOption(changeOption);
      });
    },
    getData(res) {
      // const { data: res } = await this.$http.get("map");
      this.allData = res;
      this.updateChart();
    },
    updateChart() {
      const legendArr = this.allData.map((item) => {
        return item.name;
      });
      const seriesArr = this.allData.map((item) => {
        // ‰∏Ä‰∏™Á±ªÂà´‰∏ãÁöÑÊâÄÊúâÊï£ÁÇπÊï∞ÊçÆ
        // Â¶ÇÊûúÊÉ≥Âú®Âú∞Âõæ‰∏≠ÊòæÁ§∫Êï£ÁÇπÁöÑÊï∞ÊçÆ,ÊâÄÊúâÊàë‰ª¨ÈúÄË¶ÅÁªôÊï£ÁÇπÁöÑÂõæË°®Â¢ûÂä†‰∏Ä‰∏™ÈÖçÁΩÆ,coordinateSystem:geo
        return {
          type: "effectScatter",
          name: item.name,
          data: item.children,
          coordinateSystem: "geo",
          // Ê∂üÊº™ÊïàÊûú
          rippleEffect: {
            scale: 5,
            // Ê∂üÊº™Á©∫ÂøÉÊïàÊûú
            brushType: "stroke",
          },
        };
      });
      const dataOption = {
        legend: {
          data: legendArr,
        },
        series: seriesArr,
      };
      this.chartInstance.setOption(dataOption);
    },
    screenAdapter() {
      this.titleFontSize = (this.$refs.map_ref.offsetWidth / 100) * 3.6;
      const adapterOption = {
        title: {
          textStyle: {
            fontSize: this.titleFontSize,
          },
        },
        legend: {
          itemWidth: this.titleFontSize / 2,
          itemHeight: this.titleFontSize / 2,
          itemGap: this.titleFontSize / 2,
          textStyle: {
            fontSize: this.titleFontSize / 2,
          },
        },
      };
      this.chartInstance.setOption(adapterOption);
      this.chartInstance.resize();
    },
    revertMap() {
      const revertOption = {
        geo: {
          map: "chinaMap",
        },
      };
      this.chartInstance.setOption(revertOption);
    },
  },
  computed: {
    ...mapState(["theme"]),
  },
  watch: {
    theme() {
      this.chartInstance.dispose(); // ÈîÄÊØÅÂΩìÂâçÂõæË°®
      this.initChart(); // ‰ª•ÊúÄÊñ∞ÁöÑ‰∏ªÈ¢òÂàùÂßãÂåñÂõæË°®
      this.screenAdapter(); // ÂÆåÊàêÂ±èÂπïÁöÑÈÄÇÈÖç
      this.updateChart(); // Êõ¥Êñ∞ÂõæË°®ÁöÑÂ±ïÁ§∫
    },
  },
};
</script>
