<template>
	<div class="screen-container" :style="containerStyle">
		<header class="screen-header">
			<div>
				<img :src="headerSrc" alt="">
			</div>
			<span class="logo">
				<img :src="logoSrc" alt="" />
			</span>
			<span class="title">ÂïÜÂìÅËØ¶ÊÉÖÂÆûÊó∂ÁõëÊéßÁ≥ªÁªü</span>
			<div class="title-right">
				<!-- ÁôΩÂ§©‰∏éÂ§úÈó¥Ê®°ÂºèÁöÆËÇ§ÂàáÊç¢ -->
				<img :src="themeSrc" class="qiehuan" @click="handChangeTheme">
				<span class="datetime"> {{nowTime}}</span>
			</div>
		</header>
		<div class="screen-body">
			<section class="screen-left">
				<div id="left-top" :class="[ fullScreenStatus.trend == true ? 'fullscreen' : '']">
					<!-- ÈîÄÈáèË∂ãÂäøÂõæË°® -->
					<Trend ref="trend"></Trend>
					<div class="resize">
						<span @click="changeSize('trend')"
							:class="['iconfont', fullScreenStatus.trend ? 'icon-compress-alt' : 'icon-expand-alt']"></span>
					</div>
				</div>
				<div id="left-bottom" :class="[ fullScreenStatus.seller == true ? 'fullscreen' : '']">
					<!-- ÂïÜÂÆ∂ÈîÄÂîÆÈáëÈ¢ùÂõæË°® -->
					<Seller ref="seller"></Seller>
					<div class="resize">
						<span @click="changeSize('seller')"
							:class="['iconfont', fullScreenStatus.seller ? 'icon-compress-alt' : 'icon-expand-alt']"></span>
					</div>
				</div>
			</section>
			<section class="screen-middle">
				<div id="middle-top" :class="[ fullScreenStatus.map == true ? 'fullscreen' : '']">
					<!-- ÂïÜÂÆ∂ÂàÜÂ∏ÉÂõæË°® -->
					<Map ref="map"></Map>
					<div class="resize">
						<span @click="changeSize('map')"
							:class="['iconfont', fullScreenStatus.map ? 'icon-compress-alt' : 'icon-expand-alt']"></span>
					</div>
				</div>
				<div id="middle-bottom" :class="[ fullScreenStatus.rank == true ? 'fullscreen' : '']">
					<!-- Âú∞Âå∫ÈîÄÈáèÊéíË°åÂõæË°® -->
					<Rank ref="rank"></Rank>
					<div class="resize">
						<span @click="changeSize('rank')"
							:class="['iconfont', fullScreenStatus.rank ? 'icon-compress-alt' : 'icon-expand-alt']"></span>
					</div>
				</div>
			</section>
			<section class="screen-right">
				<div id="right-top" :class="[ fullScreenStatus.hot == true ? 'fullscreen' : '']">
					<!-- ÁÉ≠ÈîÄÂïÜÂìÅÂç†ÊØîÂõæË°® -->
					<Hot ref="hot"></Hot>
					<div class="resize">
						<span @click="changeSize('hot')"
							:class="['iconfont', fullScreenStatus.hot ? 'icon-compress-alt' : 'icon-expand-alt']"></span>
					</div>
				</div>
				<div id="right-bottom" :class="[ fullScreenStatus.stock == true ? 'fullscreen' : '']">
					<!-- Â∫ìÂ≠òÈîÄÈáèÂàÜÊûêÂõæË°® -->
					<Stock ref="stock"></Stock>
					<div class="resize">
						<span @click="changeSize('stock')"
							:class="['iconfont', fullScreenStatus.stock ? 'icon-compress-alt' : 'icon-expand-alt']"></span>
					</div>
				</div>
			</section>
		</div>
	</div>
</template>

<script>
	import Hot from "@/components/Hot.vue";
	import Map from "@/components/Map.vue";
	import Rank from "@/components/Rank.vue";
	import Seller from "@/components/Seller.vue";
	import Stock from "@/components/Stock.vue";
	import Trend from "@/components/Trend.vue";
	import {
		mapState
	} from "vuex";
	import {
		getThemeValue
	} from "@/utils/theme_utils";

	export default {
		data() {
			return {
				// ÂÆö‰πâÊØè‰∏Ä‰∏™ÂõæË°®ÁöÑÂÖ®Â±èÁä∂ÊÄÅ
				fullScreenStatus: {
					trend: false,
					seller: false,
					map: false,
					rank: false,
					hot: false,
					stock: false,
				},
				// ÂΩìÂâçÊó∂Èó¥
				nowTime: "",
			};
		},
		created() {
			// Ê≥®ÂÜåÊé•Êî∂Âà∞Êï∞ÊçÆÁöÑÂõûË∞ÉÂáΩÊï∞
			this.$socket.registerCallBack("fullScreen", this.recvData);
			this.$socket.registerCallBack("themeChange", this.recvThemeChange);
		},
		destroyed() {
			// ÈîÄÊØÅÊé•Êî∂Âà∞Êï∞ÊçÆÁöÑÂõûË∞ÉÂáΩÊï∞
			this.$socket.unRegisterCallBack("fullScreen");
			this.$socket.unRegisterCallBack("themeChange");
			clearInterval(this.nowTimes);
		},
		mounted() {
			this.nowTimes();
		},
		methods: {
			changeSize(chartName) {
				// 1.ÊîπÂèòfullScreenStatusÁöÑÊï∞ÊçÆ
				// this.fullScreenStatus[chartName] = !this.fullScreenStatus[chartName];
				// 2.ÈúÄË¶ÅË∞ÉÁî®ÊØè‰∏™ÂõæË°®ÁªÑ‰ª∂‰∏≠ÁöÑÊñπÊ≥ï screenAdapter
				// this.$nextTick(() => {
				//   this.$refs[chartName].screenAdapter();
				// });
				// Â∞ÜÊï∞ÊçÆÂèëÈÄÅÁªôÊúçÂä°Á´Ø
				const targetValue = !this.fullScreenStatus[chartName];
				this.$socket.send({
					action: "fullScreen",
					socketType: "fullScreen",
					chartName: chartName,
					value: targetValue,
				});
			},
			recvData(data) {
				// ÂèñÂá∫ÊòØÂì™‰∏Ä‰∏™ÂõæË°®ËøõË°åÂàáÊç¢
				// console.log(data);
				const chartName = data.chartName;
				// ÂèñÂá∫ ÂàáÊç¢Êàê‰ªÄ‰πàÁä∂ÊÄÅ
				const targetValue = data.value;
				this.fullScreenStatus[chartName] = targetValue;
				this.$nextTick(() => {
					this.$refs[chartName].screenAdapter();
				});
			},
			handChangeTheme() {
				this.$socket.send({
					action: "themeChange",
					socketType: "themeChange",
					chartName: "",
					value: "",
				});
			},
			recvThemeChange() {
				// ‰øÆÊîπVuex‰∏≠ÁöÑÊï∞ÊçÆ
				this.$store.commit("changeTheme");
			},
			nowTimes() {
				this.timeFormate(new Date());
				setInterval(this.nowTimes, 1000);
				this.clear();
			},
			timeFormate(timeStamp) {
				let year = new Date(timeStamp).getFullYear();
				let month =
					new Date(timeStamp).getMonth() + 1 < 10 ?
					"0" + (new Date(timeStamp).getMonth() + 1) :
					new Date(timeStamp).getMonth() + 1;
				let date =
					new Date(timeStamp).getDate() < 10 ?
					"0" + new Date(timeStamp).getDate() :
					new Date(timeStamp).getDate();
				let hh =
					new Date(timeStamp).getHours() < 10 ?
					"0" + new Date(timeStamp).getHours() :
					new Date(timeStamp).getHours();
				let mm =
					new Date(timeStamp).getMinutes() < 10 ?
					"0" + new Date(timeStamp).getMinutes() :
					new Date(timeStamp).getMinutes();
				let ss =
					new Date(timeStamp).getSeconds() < 10 ?
					"0" + new Date(timeStamp).getSeconds() :
					new Date(timeStamp).getSeconds();
				let week = new Date(timeStamp).getDay();
				let weeks = ["Êó•", "‰∏Ä", "‰∫å", "‰∏â", "Âõõ", "‰∫î", "ÂÖ≠"];
				let getWeek = "ÊòüÊúü" + weeks[week];
				this.nowTime =
					year +
					"Âπ¥" +
					month +
					"Êúà" +
					date +
					"Êó•" +
					" " +
					hh +
					":" +
					mm +
					":" +
					ss +
					getWeek;
			},
			clear() {
				clearInterval(this.nowTimes);
				this.nowTimes = null;
			},
		},
		components: {
			Hot,
			Map,
			Rank,
			Seller,
			Stock,
			Trend,
		},
		computed: {
			logoSrc() {
				return "/static/img/" + getThemeValue(this.theme).logoSrc;
			},
			headerSrc() {
				return "/static/img/" + getThemeValue(this.theme).headerBorderSrc;
			},
			themeSrc() {
				return "/static/img/" + getThemeValue(this.theme).themeSrc;
			},
			containerStyle() {
				return {
					backgroundColor: getThemeValue(this.theme).backgroundColor,
					color: getThemeValue(this.theme).titleColor,
				};
			},
			...mapState(["theme"]),
		},
	};
</script>
<style lang="less" scoped>
	// ÂÖ®Â±èÊ†∑ÂºèÁöÑÂÆö‰πâ
	.fullscreen {
		position: fixed !important;
		top: 0 !important;
		left: 0 !important;
		width: 100% !important;
		height: 100% !important;
		margin: 0 !important;
		z-index: 100;
	}

	.screen-container {
		width: 100%;
		height: 100%;
		padding: 0 20px;
		background-color: #161522;
		color: #fff;
		box-sizing: border-box;
	}

	.screen-header {
		width: 100%;
		height: 64px;
		font-size: 20px;
		position: relative;

		>div {
			img {
				width: 100%;
			}
		}

		.title {
			position: absolute;
			left: 50%;
			top: 50%;
			font-size: 20px;
			transform: translate(-50%, -50%);
		}

		.title-right {
			display: flex;
			align-items: center;
			position: absolute;
			right: 0px;
			top: 50%;
			transform: translateY(-80%);
		}

		.qiehuan {
			width: 28px;
			height: 21px;
			cursor: pointer;
		}

		.datetime {
			font-size: 15px;
			margin-left: 10px;
		}

		.logo {
			position: absolute;
			left: 0px;
			top: 50%;
			transform: translateY(-80%);

			img {
				height: 40px;
				width: 110px;
				margin-top: 40px;
				margin-left: 15px;
				background-image: url(../../public/static/img/logo_light.png);
			}
		}
	}

	.screen-body {
		width: 100%;
		height: 100%;
		display: flex;
		margin-top: 10px;

		.screen-left {
			height: 100%;
			width: 27.6%;

			#left-top {
				height: 53%;
				position: relative;
			}

			#left-bottom {
				height: 31%;
				margin-top: 25px;
				position: relative;
			}
		}

		.screen-middle {
			height: 100%;
			width: 41.5%;
			margin-left: 1.6%;
			margin-right: 1.6%;

			#middle-top {
				width: 100%;
				height: 56%;
				position: relative;
			}

			#middle-bottom {
				margin-top: 25px;
				width: 100%;
				height: 28%;
				position: relative;
			}
		}

		.screen-right {
			height: 100%;
			width: 27.6%;

			#right-top {
				height: 46%;
				position: relative;
			}

			#right-bottom {
				height: 38%;
				margin-top: 25px;
				position: relative;
			}
		}
	}

	.resize {
		position: absolute;
		right: 20px;
		top: 20px;
		cursor: pointer;
	}
</style>
